function getDisplayHanViet(mean) {
  if(mean && mean["hv"]) {
    mean = mean["hv"].split(',');
    if (mean.length === 0) {
      mean = '';
    } else if (mean.length === 1) {
      mean = mean[0];
    } else {
      mean = `${mean[0]}`;
    }
   return mean;
  } else {
    GA.trackEvent('ThieuTu', 'Han', JSON.stringify( mean));
    return '';
  }
}

function processWord(chinessArray, i, dict, textScripts, bacKinhs){
  let mean;
  let word;
  console.log('processWord , chinessArray, lenWord, textScripts', chinessArray, i, textScripts);

  if(i<chinessArray.length-5){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) +
      chuanHoaChinneseNoSign(chinessArray[i+2]) + chuanHoaChinneseNoSign(chinessArray[i+3]) + chuanHoaChinneseNoSign(chinessArray[i+4]) + chuanHoaChinneseNoSign(chinessArray[i+5]);
    mean = dict[word];
    console.log('processWord word, mean', word, mean);

    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: '',
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 6;
    }
  }

  if(i<chinessArray.length-4){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) +
      chuanHoaChinneseNoSign(chinessArray[i+2])  + chuanHoaChinneseNoSign(chinessArray[i+3])  + chuanHoaChinneseNoSign(chinessArray[i+4]);
    mean = dict[word];
    console.log('processWord word, mean', word, mean);

    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1] + ' ' + bacKinhs[i+2] + ' ' + bacKinhs[i+3] + ' ' + bacKinhs[i+4],
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 5;
    }
  }

  if(i<chinessArray.length-3){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) + chuanHoaChinneseNoSign(chinessArray[i+2])  + chuanHoaChinneseNoSign(chinessArray[i+3]);
    mean = dict[word];
    console.log('processWord word, mean', word, mean);

    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1] + ' ' + bacKinhs[i+2] + ' ' + bacKinhs[i+3],
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 4;
    }
  }

  if(i<chinessArray.length-2){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) + chuanHoaChinneseNoSign(chinessArray[i+2]);
    mean = dict[word];
    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1] + ' ' + bacKinhs[i+2],
        nghiaViet:  getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 3;
    }
  }

  if(i<chinessArray.length-1){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]);
    mean = dict[word];
    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1],
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 2;
    }
  }

  mean = dict[chuanHoaChinneseNoSign(chinessArray[i])];
  let hv = ''
  if(mean) {
    hv = mean["hv"];
  }
  textScripts.push({
    id: i,
    chinesse: chinessArray[i],
    hanViet: hv,
    bacKinh: bacKinhs[i],
    nghiaViet: getDisplayHanViet(mean),
    nghias: mean?mean["nghia"]:'',
  });

  return 1;
}

function getChuHanOneWord(chinesse, dict) {
  const chinessArray = chuanHoaChinesseV2(chinesse);
  const textScripts = [];
  let mean;
  let word;
  let i = 0;

  for (i = 0; i < chinessArray.length;i++ ) {
    mean = dict[chuanHoaChinneseNoSign(chinessArray[i])];
    let hv = ''
    if(mean) {
      hv = mean["hv"];
    }
    textScripts.push({
      id: i,
      chinesse: chinessArray[i],
      hanViet: hv,
      bacKinh: '',
      nghiaViet: getDisplayHanViet(mean),
      nghias: mean?mean["nghia"]:'',
      });
    }

  return textScripts;

}

function parserScript(chinesse, dict, bacKinh) {
  const chinessArray = chuanHoaChinesseV2(chinesse);
  const textScripts = [];
  const bacKinhs = bacKinh.split(' ');
  let i = 0;

  for (i = 0; i < chinessArray.length; ) {
    i += processWord(chinessArray, i, dict, textScripts, bacKinhs);
  }

  return textScripts;

}
function parserScriptCau(cau, dict) {
  const cacDoan = replaceAll(replaceAll(replaceAll(replaceAll(cau, ',',',#'), '，', ',#'), '：',':#'), ':',':#').split('#');
  let j=0;
  let textScripts = [];
  
  for(j=0;j<cacDoan.length;j++){
    let bacKinh = pinyin(cacDoan[j]);
    let doan = parserScript(cacDoan[j], dict, bacKinh);

    console.log(cacDoan[j], doan, bacKinh);

    if(cacDoan[j]) {
      let word = doan[doan.length-1];
      if(word)
        word['dot'] = cacDoan[j].substr(cacDoan[j].length-1);
      textScripts = textScripts.concat( doan) ;
    }
  }
  if (textScripts && textScripts[0]) {
    textScripts[0]["nghiaViet"] = `${textScripts[0]["nghiaViet"].substr(0,1).toUpperCase()}${textScripts[0]["nghiaViet"].substr(1)}`;
  }
  return textScripts;
}
function updateNghiaViet(that) {
  const {typingChinesse, dict} = that.state;
  let nghiaViet = '';
  if(!typingChinesse)
    return;
  nghiaViet = xoaKiTuThuaCopyTuTrang(typingChinesse);

  for (var word in dict){
    nghiaViet = replaceAll(nghiaViet, word, dict[word].nghia );
  }

  that.setState({
    nghiaViet,
  });
}

function updateDictThayThe(that, dictThayThe) {
  let {dict} = that.state;
  
  that.setState({
    dict:dictThayThe,
    newNghia: '',
    newWord: '',
    loading: false,
  });
}

export default class DictComponent extends Component {
  static async getInitialProps({req}) {
    // Inherit standard props from the Page (i.e. with session data)
    let props = {};

    // If running on server, perform Async call
    if (typeof window === 'undefined') {
      try {
        props.dictBook = await DictBook.getDictBookData()
        props.dictTruyen = await DictBook.getDictTruyenData()
      } catch (e) {
        props.error = "Unable to fetch AsyncData on server"
      }
    }

    return props
  }

  constructor(props) {
    super(props);

    const { words } = props;

    this.state = {
      loading: true,
      nghiaViet: '',
      typingChinesse: sample,
      chinesse: '',
      doichieu: '',
      seletedDoiChieu:'',
      hanViet: '',
      bacKinh: '',
      dictChuHan: {},
      dict: {},
      blnchiHienNghiaViet: false,
      tumoiCh: '',tumoiNghia: '',tumoiReplace: ''
    };
    // axios.get('https://tv.nhidonghocphat.com/dictClient.json').then((res) => {

    axios.get('/static/baiChuHan.txt').then((res) => {
      this.setState({
        typingChinesse: xoaKiTuThuaCopyTuTrang(res.data),
      });
    });
    this.onChangetumoiCh = this.onChangetumoiCh.bind(this);
    this.onChangetumoiNghia = this.onChangetumoiNghia.bind(this);
    this.onChangetumoiReplace = this.onChangetumoiReplace.bind(this);
    this.btnThemTuMoiClick = this.btnThemTuMoiClick.bind(this);
    this.onChangeHandlerNewHanViet = this.onChangeHandlerNewHanViet.bind(this);
    this.onChangeHandlerNewNghia = this.onChangeHandlerNewNghia.bind(this);
    this.onChangeHandlerChuHan = this.onChangeHandlerChuHan.bind(this);
    this.onChangeHandlerHanViet = this.onChangeHandlerHanViet.bind(this);
    this.onChangeHandlerBacKinh = this.onChangeHandlerBacKinh.bind(this);
    this.onHtmlChangeNghiaViet = this.onHtmlChangeNghiaViet.bind(this);
    this.lamMinVanBan = this.lamMinVanBan.bind(this);
    this.trans = this.trans.bind(this);
    this.chiHienNghiaViet = this.chiHienNghiaViet.bind(this);
    this.onDoiChieuSelect = this.onDoiChieuSelect.bind(this);
    this.updateNghia = this.updateNghia.bind(this);
    this.updateTudien = this.updateTudien.bind(this);
    this.reportNew = this.reportNew.bind(this);

    this.updateTudien();

  }
  onChangetumoiCh(e) {
    this.setState({ tumoiCh: e.target.value });
  }

  onChangetumoiNghia(e) {
    this.setState({ tumoiNghia: e.target.value });
  }

  onChangetumoiReplace(e) {
    this.setState({ tumoiReplace: e.target.value });
  }
  
  btnThemTuMoiClick() {
    try{
      let {tumoiCh,tumoiNghia,tumoiReplace} = this.state;
      const dbPath = `dictTruyenHanViet/${tumoiNghia}`;

      const dbRef = ref(database);
      get(child(dbRef, dbPath)).then((snapshot) => {
        console.log('btnThemTuMoiClick tumoiCh,tumoiNghia,tumoiReplace,snapshot', tumoiCh,tumoiNghia,tumoiReplace,snapshot); 
        let tuDB = snapshot.val() || {ch:tumoiCh, thaythe: ''};
        if(tumoiCh !== tuDB.ch){
          toast.error(`Từ mới cũ chung nghĩa có chữ Hán là ${tuDB.ch} từ mới chữ Hán là ${tumoiCh}`)
          return;
        }

        tuDB.thaythe = tuDB.thaythe + ',' + tumoiReplace;
        
        const updates = {};
        updates[dbPath] = tuDB;

        update(ref(database), updates)
        .then(async () => {
          toast.success('Đã thêm từ')
        });

        this.setState({
          dict,
          dictChuHan,
          loading: false
        });
      });
    } catch (err) {
      toast.error(`Thêm từ lỗi ${err.message}`);
    }

  }

  onHtmlChangeNghiaViet(e) {
    this.setState({ nghiaViet: e.target.value });
  }

  onChangeHandlerChuHan(e) {
    this.setState({ typingChinesse: e.target.value });
  }

  onChangeHandlerHanViet(e) {
    this.setState({ hanViet: correctTyping(e.target.value) });
  }

  onChangeHandlerNewNghia(e) {
    this.setState({ newNghia: correctTyping(e.target.value) });
  }

  onChangeHandlerNewHanViet(e) {
    this.setState({ newHanViet: correctTyping(e.target.value) });
  }

  onChangeHandlerBacKinh(e) {
    this.setState({ bacKinh: correctTyping(e.target.value) });
  }

  onDoiChieuSelect(event) {
    console.log("onDoiChieuSelect");
    const selection = event.target.value.substring( event.target.selectionStart, event.target.selectionEnd);
    console.log("selection", selection);
      
    const { dict} = this.state;
    let textScripts = [];
    const cacCau = replaceAll(replaceAll(replaceAll(replaceAll(selection, '.','.#'), '. ', '.#'), '? ','?#'), '。','.#').split('#');

    let i=0;
    for(i=0;i<cacCau.length;i++){
      let cau = parserScriptCau(cacCau[i], dict);
      if(cacCau[i] && cau[cau.length-1]) {
        console.log(cacCau[i]);
        let word = cau[cau.length-1];
        cau[cau.length-1]['dot'] = cacCau[i].substr(cacCau[i].length-1);
        textScripts = textScripts.concat( cau) ;
      }
    }

    console.log("selection", selection);

    this.setState({ seletedDoiChieu: selection, chinesse: textScripts, tumoiCh:selection});
  
  }

  lamMinVanBan() {
    let {typingChinesse, dict} = this.state;
    typingChinesse = xoaKiTuThuaCopyTuTrang(typingChinesse);

    this.setState({typingChinesse: typingChinesse});
    
    console.log("lamMinVanBan");

    GA.trackEvent('Dich truyen', 'lam min', 'lam min');
  }

  reportNew() {
    const {newWord, newHanViet, newNghia} = this.state;
    const json = `{"chuHan":"${newWord}","hanViet":"${newHanViet}","bacKinh":"${pinyin(newWord)}","nghia":"${newNghia}"},`;
    GA.trackEvent("Report", "newWord", json);
    this.setState({newWord:'', newHanViet:'', newNghia:''});
    return json;
  }

  updateTudien() {
    const dictFb = ref(database, `dictTruyenHanViet`);
    onValue(dictFb, (snapshot) => {
      const dictHanViet = snapshot.val() || {};

      let dictChuHan = {};
      let dict = {};
      for (var word in dictHanViet){
        let replaceTxt = dictHanViet[word].thaythe.split(',');

        var j = 0;
        for( j=0;j<replaceTxt.length;j++){
          dict[replaceTxt[j]]  = {ch: dictHanViet[word].ch, nghia: word};
        }
      }
      
      this.setState({
        dict,
        dictChuHan,
        loading: false
      });
    });

  }

  trans() {
    let {typingChinesse, chinesse, dict, dictChuHan} = this.state;
    typingChinesse = xoaKiTuThuaCopyTuTrang(typingChinesse);
    let doichieu = '';

    // for (var word in dictChuHan){
    //   typingChinesse = replaceAll(typingChinesse, word, dictChuHan[word] );
    // }
    
    let nghiaViet = '';
    
    translate(typingChinesse, {from:'zh-CN', to: 'vi'}).then(res => {
      nghiaViet = res.text;

      console.log('dict', dict);
      for (var word in dict){
        console.log(word,replaceAll(nghiaViet, word, dict[word].nghia ),replaceAll(nghiaViet, 'Ye Guan', 'Diệp Quân' ));
        nghiaViet = replaceAll(nghiaViet, word, dict[word].nghia );
      }
      
      //Ghep cau chu Han voi Chu viet
      const cauHans = typingChinesse.split('\n');
      const cauViets = nghiaViet.split('\n');
      let cauPos = 0;
      for(cauPos = 0;cauPos<cauHans.length;cauPos++){
        doichieu += cauHans[cauPos] + '\n' + cauViets[cauPos] + '\n';
      }
    
      this.setState({
        nghiaViet,
        doichieu,
      });
      
      this.setState({nghiaViet: nghiaViet});
      //=> en
    }).catch(err => {
        console.error(err);
    });

  }

  chiHienNghiaViet() {
    let {blnchiHienNghiaViet} = this.state;
    this.setState({blnchiHienNghiaViet: !blnchiHienNghiaViet});
  }

  updateNghia(i,nghia){
    let {chinesse} = this.state;
    chinesse[i].nghiaViet = nghia;
    this.setState({chinesse});
    updateNghiaViet(this);

  }

  render() {
    const {
      nghiaViet,
      typingChinesse,
      chinesse,
      bacKinh,
      loading,
      dict,
      newWord,
      newHanViet,
      doichieu,
      seletedDoiChieu,
      newNghia,
      blnchiHienNghiaViet,
      tumoiCh,tumoiNghia,tumoiReplace
    } = this.state;

    // Build Cards for Listing
    let chuHans = '', i = 0;
    if (loading) {
      chuHans = <Loading />;
    } else if (chinesse) {
      chuHans = chinesse.map(item => {
        return (
          <ChuHan
            id={i++}
            key={Math.random()}
            chinesse={item.chinesse}
            hanViet={item.hanViet}
            bacKinh={item.bacKinh}
            nghias={item.nghias}
            nghiaViet={item.nghiaViet}
            updateNghia={this.updateNghia}
            dot={item.dot}
          />
        );
      });
    }
    return (
      <div>
        <Row>
          <h2 className="h1Title">Dịch truyện</h2>
          {blnchiHienNghiaViet && <div>${nghiaViet}</div>}
          <button className="btnProcess" onClick={this.chiHienNghiaViet} style={{marginLeft:100}}>Chỉ hiện nghĩa Việt</button>
        </Row>
        {!blnchiHienNghiaViet && <Row style={{paddingTop: '10px'}}>
          <a href="https://docs.google.com/document/d/e/2PACX-1vQai_CXAKESaTwRgmoenHEKXP9mymw4NGn7oUVt6_OPwxH663qHs_Pu6K7hjhIVk85IKwR2Wq3Afe0X/pub" target="_blank">wordDich</a>
          <a href="https://www.feibzw.com/html/35181/index.html" target="_blank">_Mục Lục</a>
          <a href="https://tamlinh247.vn/truyen-hau-due-kiem-than/" target="_blank">_TamLinh247</a>
          Đối chiếu
          <a href="https://www.feibzw.com/html/35181/35649573.html" target="_blank">八百四十四</a>
          <a href="https://tamlinh247.vn/truyen-hau-due-kiem-than/chuong-3376/" target="_blank">chuong-3376</a>
        </Row>}
        {!blnchiHienNghiaViet && <Row>
          <FormGroup style={{width:'100%'}}>
            <Label for="inputChuHan">Nhập nội dung chữ Hán</Label>
            <TextareaAutosize
              className="txtAutoSize"
              id="inputChuHan"
              placeholder="Chữ Hán"
              value={typingChinesse}
              onChange={this.onChangeHandlerChuHan}
            />
          </FormGroup>
        </Row>}
        <Row style={{paddingTop: '10px'}}>
          <div style={{width:'100%', textAlign:'center'}}>
            <button className="btnProcess" onClick={this.updateTudien} style={{marginLeft:10}}>Lấy từ điển</button>
            <button className="btnProcess" onClick={this.trans} style={{marginLeft:10}}>Dịch</button>
            <button className="btnProcess" onClick={this.chiHienNghiaViet} style={{marginLeft:10}}>Nghe đọc</button>
            Thêm từ mới
            tìm <input value={tumoiReplace} onChange={this.onChangetumoiReplace}></input>
            chinese <input value={tumoiCh} onChange={this.onChangetumoiCh}></input>
            nghĩa <input value={tumoiNghia} onChange={this.onChangetumoiNghia}></input>
            <button className="btnProcess" onClick={this.btnThemTuMoiClick} style={{marginLeft:10}}>Thêm</button>
          </div>
        </Row>
        {!blnchiHienNghiaViet && <Row style={{paddingTop: '10px'}}>
          <h4>Đối Chiếu</h4>{chuHans}
          <TextareaAutosize
            className="txtAutoSize"
            id="inputDoiChieu"
            placeholder="dịch nghĩa"
            value={doichieu}
            onChange={this.onHtmlChangeNghiaViet}
            onSelect={this.onDoiChieuSelect}
          />
        </Row>}
        {!blnchiHienNghiaViet && <Row style={{paddingTop: '10px'}}>
          <h4>Chỉnh sửa lại nội dung bản dịch tại đây</h4>
          <TextareaAutosize
            className="txtAutoSize"
            id="inputNghiaViet"
            placeholder="dịch nghĩa"
            value={nghiaViet}
            onChange={this.onHtmlChangeNghiaViet}
          />
        </Row>}
      </div>
    );
  }
}


